lata-font: \"Inconsolata\";\n  --static-inconsolata-regular-font: \"Static Inconsolata Regular\";\n  --static-inconsolata-bold-font: \"Static Inconsolata Bold\";\n  /*Inter Font*/\n  --inter-font: \"Inter\";\n  --static-inter-regular-font: \"Static Inter Regular\";\n  --static-inter-bold: \"Static Inter Bold\";\n  /*Lora Font*/\n  --lora-font: \"Lora\";\n  --lora-static-regular-font: \"Static Lora Regular\";\n  --lora-static-bold-font: \"Static Lora Bold\";\n  --lora-italic-font: \"Lora Italic\";\n  --lora-italic-bold-font: \"Static Lora Bold Italic\";\n}\n\n/*Font Face Typography*/\n/*Inconsolata Font*/\n@font-face {\n  font-family: \"Inconsolata\";\n  src: url(\"../assets/fonts/inconsolata/Inconsolata-VariableFont_wdth\\,wght.woff2\")\n    format(\"woff2\"); /* Modern Browsers*/\n  src: url(\"../assets/fonts/inconsolata/Inconsolata-VariableFont_wdth\\,wght.woff\")\n    format(\"woff\");\n  src: url(\"../assets/fonts/inconsolata/Inconsolata-VariableFont_wdth\\,wght.ttf\")\n    format(\"truetype\"); /*Fallback*/\n  font-weight: 200 900;\n}\n\n/*Static Inconsolata Regular*/\n@font-face {\n  font-family: \"Static Inconsolata Regular\";\n  src: url(\"../assets/fonts/inconsolata/static/Inconsolata-Regular.woff2\")\n    format(\"woff2\"); /*Modern Browsers*/\n  src: url(\"../assets/fonts/inconsolata/static/Inconsolata-Regular.woff\")\n    format(\"woff\");\n  src: url(\"../assets/fonts/inconsolata/static/Inconsolata-Regular.ttf\")\n    format(\"truetype\"); /*Fallback*/\n  font-weight: 400;\n}\n\n/*Static Inconsolata Bold*/\n@font-face {\n  font-family: \"Static Inconsolata Bold\";\n  src: url(\"../assets/fonts/inconsolata/static/Inconsolata-Bold.woff2\")\n    format(\"woff2\"); /*Modern Browsers*/\n  src: url(\"../assets/fonts/inconsolata/static/Inconsolata-Bold.woff\")\n    format(\"woff\");\n  src: url(\"../assets/fonts/inconsolata/static/Inconsolata-Bold.ttf\")\n    format(\"truetype\"); /*Fallback*/\n  font-weight: 600 800;\n}\n\n/*Inter Font*/\n@font-face {\n  font-family: \"Inter\";\n  src: url(\"../assets//fonts/inter/Inter-VariableFont_slnt\\,wght.woff2\")\n    format(\"woff2\"); /* Modern Browsers*/\n  src: url(\"../assets//fonts/inter/Inter-VariableFont_slnt\\,wght.woff\")\n    format(\"woff\");\n  src: url(\"../assets/fonts/inter/Inter-VariableFont_slnt\\,wght.ttf\")\n    format(\"truetype\"); /*Fallback*/\n  font-weight: 200 900;\n}\n\n/*Static Inter Regular*/\n@font-face {\n  font-family: \"Static Inter Regular\";\n  src: url(\"../assets//fonts/inter/static/Inter-Regular.woff2\") format(\"woff2\"); /* Modern Browsers*/\n  src: url(\"../assets//fonts/inter/static/Inter-Regular.woff\") format(\"woff\");\n  src: url(\"../assets/fonts/inter/static/Inter-Regular.ttf\") format(\"truetype\"); /*Fallback*/\n  font-weight: 200 900;\n}\n\n/*Static Inter Bold*/\n@font-face {\n  font-family: \"Static Inter Bold\";\n  src: url(\"../assets//fonts/inter/static/Inter-Bold.woff2\") format(\"woff2\"); /* Modern Browsers*/\n  src: url(\"../assets//fonts/inter/static/Inter-Bold.woff\") format(\"woff\");\n  src: url(\"../assets/fonts/inter/static/Inter-Bold.ttf\") format(\"truetype\"); /*Fallback*/\n  font-weight: 200 900;\n}\n\n/*Lora Font*/\n@font-face {\n  font-family: \"Lora\";\n  src: url(\"../assets//fonts/lora/Lora-VariableFont_wght.woff2\") format(\"woff2\"); /* Modern Browsers*/\n  src: url(\"../assets/fonts/lora/Lora-VariableFont_wght.woff\") format(\"woff\");\n  src: url(\"../assets/fonts/lora/Lora-VariableFont_wght.ttf\") format(\"truetype\"); /*Fallback*/\n  font-weight: 400 700;\n}\n\n/*Static Lora Regular*/\n@font-face {\n  font-family: \"Static Lora Regular\";\n  src: url(\"../assets/fonts/lora/static/Lora-Regular.woff2\") format(\"woff2\"); /* Modern Browsers*/\n  src: url(\"../assets//fonts/lora/static/Lora-Regular.woff\") format(\"woff\");\n  src: url(\"../assets/fonts/lora/static/Lora-Regular.ttf\") format(\"truetype\"); /*Fallback*/\n  font-weight: 400;\n}\n\n/*Static Lora Bold*/\n@font-face {\n  font-family: \"Static Lora Bold\";\n  src: url(\"../assets/fonts/lora/static/Lora-Bold.woff2\") format(\"woff2\"); /* Modern Browsers*/\n  src: url(\"../assets/fonts/lora/static/Lora-Bold.woff\") format(\"woff\");\n  src: url(\"../assets/fonts/lora/static/Lora-Bold.ttf\") format(\"truetype\"); /*Fallback*/\n  font-weight: 700;\n}\n\n/*Lora Italic Font*/\n@font-face {\n  font-family: \"Lora Italic\";\n  src: url(\"../assets/fonts/lora/Lora-Italic-VariableFont_wght.woff2\")\n    format(\"woff2\"); /* Modern Browsers*/\n  src: url(\"../assets/fonts/lora/Lora-Italic-VariableFont_wght.woff\")\n    format(\"woff\");\n  src: url(\"../assets/fonts/lora/Lora-Italic-VariableFont_wght.ttf\")\n    format(\"truetype\"); /*Fallback*/\n  font-weight: 400 700;\n}\n\n/*Static Lora Bold Italic*/\n@font-face {\n  font-family: \"Static Lora Bold Italic\";\n  src: url(\"../assets/fonts/lora/static/Lora-BoldItalic.woff2\") format(\"woff2\"); /* Modern Browsers*/\n  src: url(\"../assets/fonts/lora/static/Lora-BoldItalic.woff\") format(\"woff\");\n  src: url(\"../assets/fonts/lora/static/Lora-BoldItalic.ttf\") format(\"truetype\"); /*Fallback*/\n  font-weight: 700;\n}\n\nbody,\nhtml {\n  margin: 0;\n  padding: 0;\n  box-sizing: border-box;\n}\n\n/*Fonts*/\n#font-dropdown-list p,\n#font-choice-btn {\n  font-size: 14px;\n  line-height: 24px;\n  color: var(--off-black-one);\n}\n\n/*Font Dropdown List*/\n#font-dropdown-list p {\n  position: relative;\n  top: 10px;\n  left: 30px;\n  width: 90px;\n  height: 24px;\n  cursor: pointer;\n}\n\n#font-dropdown-list {\n  background-color: var(--white);\n  width: 183px;\n  height: 140px;\n  border-radius: 16px;\n  box-shadow: var(--box-shadow);\n  display: none;\n}\n\n/*Font Dropdown List Hover*/\n#font-dropdown-list p:hover {\n  color: var(--purple);\n}\n\n/*Font Button*/\n#font-choice-btn {\n  display: flex;\n  flex-direction: row;\n  width: 100%;\n  appearance: none;\n  border: none;\n  background-color: transparent;\n}\n\n#arrow-svg {\n  display: block;\n  padding-left: 20px;\n  padding-top: 8px;\n}\n\n/*Font Types*/\n#sans-serif {\n  font-family: var(--inter-font), var(--static-inter-bold);\n  font-weight: bold;\n}\n\n#serif {\n  font-family: var(--lora-font), var(--lora-static-bold-font);\n  font-weight: bold;\n}\n\n#mono {\n  font-family: var(--inconsolata-font), var(--static-inconsolata-bold-font);\n  font-weight: bold;\n}\n\n/*Align Header Items*/\nheader {\n  display: flex;\n  justify-content: center;\n  margin-top: 20px;\n}\n\n/*Book Logo*/\n#book-logo {\n  width: 28.05px;\n  height: 32px;\n  position: relative;\n  right: 30px;\n}\n\n.toggle-switch-and-moon-icon,\n#font-dropdown-menu {\n  position: relative;\n  left: 60px;\n  margin-left: 25px;\n}\n\n#font-dropdown-list {\n  position: absolute;\n}\n\n#divide-line {\n  position: relative;\n  left: 60px;\n  margin-right: -15px;\n}\n\n#main-header-content {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  position: relative;\n  right: 20px;\n}\n\n/*Moon Icon*/\n#moon-icon {\n  width: 19.99px;\n  height: 20px;\n  margin-left: 8px;\n}\n\n/*Moon Icon Group*/\n.toggle-switch-and-moon-icon {\n  display: flex;\n}\n\n/*Moon Cover Hover*/\n#moon-icon path:hover {\n  stroke: var(--purple);\n}\n\n/* Search Bar Input Field*/\nform {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  margin-top: 20px;\n}\n\n/*Add custom icon search bar*/\ninput[type=\"search\"] {\n  appearance: none;\n  width: 327px;\n  height: 48px;\n  border-radius: 16px;\n  background-color: var(--off-white-one);\n  border: 0.5px solid var(--off-white-one);\n  background-image: url(\"../assets/images/icon-search.svg\");\n  background-repeat: no-repeat;\n  background-position: 95% center;\n  cursor: pointer;\n}\n\n/*Remove default x symbol*/\ninput[type=\"search\"]::-webkit-search-cancel-button {\n  --webkit-appearance: none;\n  appearance: none;\n  opacity: 0;\n}\n\n/*Add border color on active for input field*/\ninput[type=\"search\"]:focus,\n#valid-value {\n  border: 1px solid var(--purple);\n  outline: none;\n}\n\n/*Set Invalid Border Color from JavaScript*/\n#invalid-search-field {\n  border: 1px solid var(--red);\n  outline: none;\n}\n\n#invalid-error-message {\n  color: var(--red);\n  position: relative;\n  top: 5px;\n  right: 40px;\n  width: 244px;\n  height: 24px;\n  font-family: var(--inter-font), var(--static-inter-regular-font);\n  font-size: 14px;\n  line-height: 17px;\n}\n\n/*Add dividing Line on header*/\n#divide-line {\n  width: 1px;\n  height: 32px;\n  background-color: var(--gray-num-two);\n  z-index: -1; /*places behind list*/\n}\n\n/*Toggle Switch*/\n.switch {\n  position: relative;\n  display: inline-block;\n  width: 40px;\n  height: 20px;\n}\n\n/*Hide input box to\n replace with own style*/\n.switch input {\n  opacity: 0;\n  width: 0;\n  height: 0;\n}\n\n.slider {\n  position: absolute;\n  cursor: pointer;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  background-color: var(--gray-num-one);\n  -webkit-transition: 0.4s;\n  transition: 0.4s;\n}\n\n/*Add white circle for toggle*/\n.slider:before {\n  position: absolute;\n  content: \"\";\n  height: 14px;\n  width: 14px;\n  left: 4px;\n  bottom: 4px;\n  background-color: var(--white);\n  -webkit-transition: 0.4s;\n  transition: 0.4s;\n}\n\n/*Add box shadow on toggle input*/\ninput:focus + .slider {\n  box-shadow: var(--box-shadow);\n}\n\n/*Toggle Slider*/\ninput:checked + .slider:before {\n  --webkit-transform: translateX(16px);\n  -ms-transform: translate(16px);\n  transform: translate(16px);\n}\n\n/*Change background color on toggle when checked*/\ninput:checked + .slider.round {\n  background-color: var(--purple);\n}\n\n/*Slider Hover Box Shadow*/\n.slider.round:hover {\n  box-shadow: var(--box-shadow);\n}\n\n/*Round the slider background*/\n.slider.round {\n  border-radius: 34px;\n}\n\n/*Round the slider*/\n.slider.round:before {\n  border-radius: 50%;\n}\n\n/*Dark and Light Mode Theme Toggle*/\n.dark-mode {\n  background-color: var(--black-num-one);\n  color: var(--white);\n}\n\n.light-mode {\n  background-color: var(--white);\n  color: var(--black-num-one);\n}\n\n/*Moon Icon Dark Theme*/\n.moon-dark-theme path {\n  stroke: var(--purple);\n}\n\n/*Dictionary Words*/\n/*Phonetics, noun and noun-line*/\n#phonetic {\n  margin-top: -20px;\n}\n\n#parts-of-speech-and-line-group,\n#parts-of-speech-and-line-group-two {\n  display: flex;\n  justify-content: space-evenly;\n  align-items: center;\n}\n\n/*Audio*/\n#play-icon,\n#play-icon-two {\n  width: 48px;\n  height: 48px;\n}\n\n/*Play Icons Cursor*/\n#play-icon,\n#play-icon-two {\n  cursor: pointer;\n}\n\n/*Play Icons with arrows on hover*/\n#play-icon g:hover,\n#play-icon-two g:hover {\n  fill: var(--white);\n}\n\n#play-icon circle:hover,\n#play-icon-two circle:hover {\n  fill: var(--purple);\n  opacity: 100;\n}\n\n/*Align play icon\n with vocab word*/\n#play-icon-two{\n  margin-top: -100px;\n}\n\n/*Word and Phonetics*/\n.word-and-sound,\n.word-and-sound-two {\n  display: flex;\n  justify-content: space-around;\n  align-items: center;\n  gap: 150px;\n}\n\n#play-icon,\n#play-icon-two,\n#style-line,\n#style-line-two #style-line-three,\n#style-line-four {\n  display: none;\n}\n\n/*Definitions List*/\n/*Meanings-Text*/\n.meaning-text,\n.meaning-text-two {\n  margin-top: -20px;\n}\n\n/*Synonyms Text*/\n#synonyms-text,\n#synonyms-text-two {\n  margin-top: 10px;\n}\n\n/*Both Meaning and \nSynonyms Text*/\n.meaning-text,\n.meaning-text-two,\n#synonyms-text,\n#synonyms-text-two {\n  margin-left: 20px;\n}\n\n/*Synonyms Lists*/\n#synonym-one,\n#synonym-two,\n#synonym-three,\n#synonym-one-of-two,\n#synonym-two-of-two,\n#synonym-three-of-two {\n  margin-left: 230px;\n}\n\nul {\n  list-style: none;\n}\n\nul li:before {\n  content: \"\\2022\";\n  color: var(--purple);\n  font-weight: bold;\n  display: inline-block;\n  width: 1em;\n  margin-left: -1em;\n}\n\n#def-one,\n#def-two,\n#def-three,\n#def-one-of-two,\n#def-two-of-two,\n#def-three-of-two {\n  margin: 20px;\n